

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

// Nav style

a.navbar-brand {
  font-family: 'montserrat' sans-serif;
  }
nav{
  div.container {
    margin: 0;
    padding-left: 0;
    
  }
  
  .bg-light {
    background-color: none;
  }
  button.navbar-toggler.collapsed {
    border: none;
  }
  .logout {
    border: none;
  }
}
.navbar-light .navbar-toggler:focus {
  box-shadow:none;
  border: none;
}
.navbar-light .navbar-toggler {
  box-shadow:none;
  border: none;
}
.nav-bar-profile-image {
  border-radius: 50%;
}

// header

div.header.container {
  margin: 0;
  margin-top: -40px;
  font-size: 600%;
  padding: 0;
  font-family: 'Montserrat', sans-serif ;
}

div.card-title.h5 {
  font-family: 'Montserrat', sans-serif ;
}
// Card style 

div.container-fluid {
  display: flex;
  flex-wrap: wrap;
  max-width: 2000px;
  justify-content: center;
  
  div.row {
    margin-top: 0;
    margin-left: 0;
    margin-bottom: auto;
    margin-top: auto;
  }
  .row {
    margin: 0;
    --bs-gutter-x: 0rem;
  }
  div.card {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 2px;
    
  }
  .card {
  border-radius: 0;
  border: none;
  background-color:rgb(246, 248, 250);
  }
  img {
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

// Show style 

div.show_style.container {
  margin-left: 0;
  margin-top: 5%;

div.inner_row.row {
  display: flex;
    justify-content: flex-start;
}
.detail {
  

  div.normal_row.row {
    border-bottom: 1px solid black;
  }
  div.user_is_owner.row {
    border-bottom: 1px solid black;
  }
}

.inner_row {
  img {
    object-fit: cover;
  }

}
.buttons {
  
  display: flex;
  justify-content: flex-end;
  align-items: center;
  
  a.nav-link {
    color:black;
    a:hover {
      box-shadow: 1% gray;
    }
  }

  button.delete.btn.btn-link {
    text-decoration: none;
    color:black
  }
}



}
// Form 

div.form_container.container {
  background-color:rgb(246, 248, 250);
  display: flex;
  justify-content: center;
  padding: 5%;
  max-width: 400px;
  
  
    .form_field {
        max-width: 350px;
  }
  button {
    text-decoration: none;
    color: black;
    margin-top: 1%;
    font-weight: bold;
  }
}
// Comments
div.comments.row {
  border-top: 1px solid black;
  margin-top: 5%;
  max-width: 400px;
  
}
.form-control {
  border-radius: none;
}
div.comments_row.row {
  border-top: 1px solid black;
  
}